// Initialize
window.addEventListener("load", initialize);

function initialize() {
  console.log("Welcome! 👋");
  getSourceData();
  getPage();
  getSearchText();
  getAdvanceOption();
  getContactEmail();
  getNetworkStatus();
}

// Page
function getPage() {
  let menu = document.querySelector("button#menu");

  if (menu) {
    menu.addEventListener("click", () => {
      let overlayItem = document.querySelector("#info[data-open]");

      if (overlayItem.getAttribute("data-open") == "false") {
        overlayItem.setAttribute("data-open", "true");
        overlayItem.previousElementSibling.setAttribute("data-open", "false");
        menu.textContent = "Close Info";
      } else {
        overlayItem.setAttribute("data-open", "false");
        overlayItem.previousElementSibling.setAttribute("data-open", "true");
        menu.textContent = "Info";
      }
    });
  }
}

// Email
function getContactEmail() {
  let contactItems = document.querySelectorAll("[data-email]");
  let contactArray = Array.from(contactItems);

  contactArray.forEach((contact) => {
    document.addEventListener("click", (event) => {
      getContactEvent(event);
    });

    document.addEventListener("keydown", (event) => {
      if (event.key === "Enter") {
        getContactEvent(event);
      }
    });

    function getContactEvent(event) {
      if (contact.id == event.target.id) {
        let emailData = "helloATfebreroDOTinfo";
        emailData = emailData.replace(/AT/, "@").replace(/DOT/, ".");
        contact.setAttribute("data-email", "true");
        contact.setAttribute("href", `mailto:${emailData}`);
      } else {
        contact.setAttribute("data-email", "false");
        contact.removeAttribute("data-value");
        contact.removeAttribute("href");
        contact.removeAttribute("rel");
        contact.removeAttribute("target");
      }
    }
  });
}

// Network
function getNetworkStatus() {
  let status = document.querySelector("#status");
  let online = document.querySelector("#online");
  let offline = document.querySelector("#offline");

  function updateNetworkStatus() {
    let condition = navigator.onLine ? "online" : "offline";

    if (condition === "offline") {
      status.setAttribute("data-open", "true");
      offline.setAttribute("data-open", "true");
      online.setAttribute("data-open", "false");
    } else {
      status.setAttribute("data-open", "true");
      online.setAttribute("data-open", "true");
      offline.setAttribute("data-open", "false");

      setTimeout(() => {
        online.setAttribute("data-open", "false");
        status.setAttribute("data-open", "false");
      }, 5800);
    }
  }

  window.addEventListener("online", updateNetworkStatus);
  window.addEventListener("offline", updateNetworkStatus);
}

// Finder
const combinedPageData = [];
const combinedImageData = [];

function getSourceData() {
  displayLoader();
  fetchData();
  fetchImage();
}

function getStoredData() {
  let storedData = sessionStorage.getItem("data");
  let storedImage = sessionStorage.getItem("image");

  if (storedData && storedImage) {
    mergeDataSource();
    hideLoader();
    getInfo();
  }
}

function fetchData() {
  let storedData = sessionStorage.getItem("data");
  if (!storedData) {
    let pages = 29;

    Array(pages)
      .fill(0)
      .forEach((index, page) => {
        // "Powered by Stratonauts Dog API"
        fetch(`https://dogapi.dog/api/v2/breeds?page[number]=${page + 1}`, {
          cache: "no-cache",
        })
          .then((response) => response.json())
          .then((breeds) => {
            let data = breeds.data;
            data.forEach((breed) => {
              combinedPageData.push(breed.attributes);
            });
            sessionStorage.setItem("data", JSON.stringify(combinedPageData));
            getStoredData();
          })
          .catch((error) => {
            console.log("Error Data Source =>", error);
          });
      });
  } else {
    getStoredData();
  }
}

function fetchImage() {
  let storedImage = sessionStorage.getItem("image");

  if (!storedImage) {
    fetch("./assets/data/collection.json", {
      cache: "no-cache",
    })
      .then((response) => response.json())
      .then((images) => {
        let image;
        images.forEach((breed) => {
          image = {
            name: breed.name,
            image_url: breed.image,
            image_wiki: breed.wiki,
          };
          combinedImageData.push(image);
        });
        sessionStorage.setItem("image", JSON.stringify(combinedImageData));
        getStoredData();
      })
      .catch((error) => {
        console.log("Error Image Source =>", error);
      });
  } else {
    getStoredData();
  }
}

function mergeDataSource() {
  let storedData = JSON.parse(sessionStorage.getItem("data"));
  let storedImage = JSON.parse(sessionStorage.getItem("image"));

  if (storedData && storedImage) {
    // Based on https://medium.com/ramsatt/how-to-merge-two-array-object-based-on-key-values-6562d982c1f4
    let combineArray = (firstArray, secondArray) =>
      firstArray.map((firstItem) => ({
        ...secondArray.find(
          (secondItem) => secondItem.name === firstItem.name && secondItem
        ),
        ...firstItem,
      }));

    let combinedArray = combineArray(storedData, storedImage);

    return combinedArray;
  }
}

function getSearchText() {
  let search = document.querySelector("#search");

  search.addEventListener("keyup", () => {
    let textEntry = search.value;
    let textCount = textEntry.length;

    if (textCount >= 3) {
      let textEntryArray = textEntry.split(" ");

      let searchText = textEntryArray
        .map((text) => {
          if (text) {
            return text[0].toUpperCase() + text.slice(1);
          }
        })
        .join(" ");

      getSearchData(searchText);
    }

    if (textCount < 2) {
      resetSearchDataMap();
      getInfo();
      resetFocus();
    }
  });

  if (search.value == "") {
    getInfo();
  }
}

function getInfo() {
  let result = document.querySelector("#result");

  result.classList.add("breed__error");
  result.innerHTML = `
  <p>The <strong>dog</strong> (<strong>Canis familiaris </strong> or <strong>Canis lupus familiaris</strong>) is a domesticated descendant of the wolf. Also called the domestic dog, it is derived from the extinct Pleistocene wolf and the modern wolf is the dog's nearest living relative. Dogs were the first species to be domesticated by hunter-gatherers over 15,000 years ago.</p>
  `;
}

function getFocus() {
  let active = document.querySelector(".breed__icon");

  active.setAttribute("data-active", "true");
}

function resetFocus() {
  let active = document.querySelector(".breed__icon");

  active.removeAttribute("data-active");
}

function getAdvanceOption() {
  let option = document.querySelector("#option");

  option.addEventListener("click", () => {
    if (option.textContent == "View All") {
      getAllData();
      option.textContent = "Clear All";
    } else {
      resetSearchDataMap();
      getInfo();
      option.textContent = "View All";
    }
  });
}

function getAllData() {
  let breeds = mergeDataSource();

  mapSearchData(breeds);
}

function getSearchData(searchText) {
  let sourceArray = mergeDataSource();

  let breeds = sourceArray.filter((breed) => {
    return breed.name.includes(searchText);
  });

  mapSearchData(breeds);
  getFocus();

  if (breeds.length <= 0) {
    errorSearchDataMap();
  }
}

async function mapSearchData(breeds) {
  if (breeds) {
    breeds = breeds.sort((a, b) => {
      if (a.name < b.name) {
        return -1;
      }
      if (a.name > b.name) {
        return 1;
      }
      return 0;
    });

    resetSearchDataMap();

    let result = document.querySelector("#result");

    breeds.map((breed, index) => {
      let article = document.createElement("article");
      article.setAttribute("class", "breed__item");

      let articleImageContainer = document.createElement("div");
      articleImageContainer.setAttribute("class", "breed__image");

      if (breed.image_url) {
        let articleImage = document.createElement("img");
        articleImage.setAttribute("src", breed.image_url);
        articleImage.setAttribute("alt", `${breed.name} Breed`);

        articleImageContainer.appendChild(articleImage);
        article.appendChild(articleImageContainer);
      } else {
        let articlePlaceholder = document.createElement("p");
        articlePlaceholder.textContent = "No Image Found";
        articleImageContainer.appendChild(articlePlaceholder);
        article.appendChild(articleImageContainer);
      }

      let articleFront = document.createElement("div");
      articleFront.setAttribute("class", "breed__front");
      articleFront.setAttribute("data-open", "true");

      let articleName = document.createElement("h2");
      articleName.textContent = breed.name;

      let articleDescription = document.createElement("p");
      articleDescription.textContent = breed.description;

      let articleExpand = document.createElement("button");
      articleExpand.id = `expand_${index}`;
      articleExpand.setAttribute("class", "breed__expand");
      articleExpand.textContent = "Open More Information";

      let articleBack = document.createElement("div");
      articleBack.setAttribute("class", "breed__back");
      articleBack.setAttribute("data-open", "false");

      let articleLifespanHeader = document.createElement("h3");
      articleLifespanHeader.textContent = "Life Expectancy";

      let articleLifespan = document.createElement("p");
      articleLifespan.textContent = `${Object.values(breed.life)[1]} - ${
        Object.values(breed.life)[0]
      } years`;

      let articleWeightContainer = document.createElement("div");
      articleWeightContainer.setAttribute("class", "breed__weight");

      let articleWeightHeader = document.createElement("h3");
      articleWeightHeader.textContent = "Weight";

      let articleMaleWeight = document.createElement("p");
      articleMaleWeight.textContent = `Male: ${
        Object.values(breed.male_weight)[1]
      } - ${Object.values(breed.male_weight)[0]} kg`;

      let articleFemaleWeight = document.createElement("p");
      articleFemaleWeight.textContent = `Female: ${
        Object.values(breed.female_weight)[1]
      } - ${Object.values(breed.female_weight)[0]} kg`;

      let articleHypoallergenicHeader = document.createElement("h3");
      articleHypoallergenicHeader.textContent = "Hypoallergenic";

      let hypoAllergenicStatus = JSON.stringify(breed.hypoallergenic);
      let status;

      let statusCapitalized =
        hypoAllergenicStatus.charAt(0).toUpperCase() +
        hypoAllergenicStatus.slice(1);

      if (hypoAllergenicStatus == "true") {
        status = "Yes";
      } else {
        status = "No";
      }

      let articleHypoallergenic = document.createElement("p");
      articleHypoallergenic.textContent = status;

      let articleLinkContainer = document.createElement("div");
      articleLinkContainer.setAttribute("class", "breed__links");

      let name = breed.name.replace(/\s/g, "_");
      let articleLink = document.createElement("a");
      articleLink.setAttribute("href", `https://en.wikipedia.org/wiki/${name}`);
      articleLink.setAttribute("target", "_blank");
      articleLink.setAttribute("rel", "noreferrer noopener");
      articleLink.textContent = `${breed.name} Wikipedia`;

      let articleRedirect = document.createElement("a");
      articleRedirect.setAttribute(
        "href",
        "https://en.wikipedia.org/wiki/List_of_dog_breeds"
      );
      articleRedirect.setAttribute("target", "_blank");
      articleRedirect.setAttribute("rel", "noreferrer noopener");
      articleRedirect.textContent = `Dog Breeds Information`;

      articleFront.appendChild(articleName);
      articleFront.appendChild(articleDescription);
      article.appendChild(articleFront);

      articleBack.appendChild(articleLifespanHeader);
      articleBack.appendChild(articleLifespan);
      articleBack.appendChild(articleWeightContainer);
      articleWeightContainer.appendChild(articleWeightHeader);
      articleWeightContainer.appendChild(articleMaleWeight);
      articleWeightContainer.appendChild(articleFemaleWeight);
      articleBack.appendChild(articleHypoallergenicHeader);
      articleBack.appendChild(articleHypoallergenic);
      articleLinkContainer.appendChild(articleLink);
      articleLinkContainer.appendChild(articleRedirect);
      articleBack.appendChild(articleLinkContainer);
      article.appendChild(articleBack);
      article.appendChild(articleExpand);

      result.appendChild(article);

      articleExpand.addEventListener("click", () => {
        let top = articleExpand.previousElementSibling.previousElementSibling;
        let bottom = articleExpand.previousElementSibling;

        if (top.getAttribute("data-open") == "false") {
          top.setAttribute("data-open", "true");
          bottom.setAttribute("data-open", "false");
          articleExpand.textContent = "Open More Information";
        } else {
          top.setAttribute("data-open", "false");
          bottom.setAttribute("data-open", "true");
          articleExpand.textContent = "Close Information";
        }
      });
    });
  }
}

// Reset
function resetSearchDataMap() {
  let result = document.querySelector("#result");
  let option = document.querySelector("#option");

  if (result.hasChildNodes()) {
    while (result.firstChild) {
      result.removeChild(result.firstChild);
      option.textContent = "View All";
    }
  }

  if (result.classList.contains("breed__error")) {
    result.classList.remove("breed__error");
  }
}

// Error
function errorSearchDataMap() {
  let result = document.querySelector("#result");
  result.classList.add("breed__error");

  let articleError = document.createElement("span");
  articleError.innerHTML = `
  <h2>Oops, No information is available.</h2> 
  <p>Please check spelling and try again.</p>`;
  result.appendChild(articleError);
}

// Loader
function displayLoader() {
  let result = document.querySelector("#result");
  result.classList.add("breed__loader");

  let articleLoader = document.createElement("div");
  articleLoader.setAttribute("class", "loader");
  articleLoader.innerHTML = `
  <span class="loader__item"></span>
  <span class="loader__item"></span>
  <span class="loader__item"></span>
  <span class="loader__item"></span>
  <span class="loader__item"></span>`;
  result.appendChild(articleLoader);
}

function hideLoader() {
  console.log("!", "hide");
  let result = document.querySelector("#result");
  result.classList.remove("breed__loader");

  resetSearchDataMap();
}
